          { name: '‰æõÂ∫îÂïÜ', value: 40 },
          { name: 'Áîü‰∫ßÈÉ®', value: 35 },
          { name: 'ÂìÅÊéßÈÉ®', value: 15 },
          { name: 'ÁÆ°ÁêÜÂëò', value: 10 }
