    // Á°ÆËÆ§È™åËØÅ
    async confirmAuth() {
      if(!this.authPassword) {
        this.$refs.toast.show({
          type: 'warning',
          message: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å'
        })
        return
