        // TODO: È™åËØÅÂØÜÁ†Å
        await new Promise(resolve => setTimeout(resolve, 500))
        
        this.showAuth = false
        this.authPassword = ''
        
        if(this.pendingAction) {
          this.pendingAction()
          this.pendingAction = null
        }
      } catch(e) {
        this.$refs.toast.show({
          type: 'error',
          message: 'È™åËØÅÂ§±Ë¥•'
